export default {
  data() {
    return {
      showStatus: true,
      editStatus: false,
      reviewStatus: false,
      activeName: 'first',
      // 品牌名
      brandName: [{
        value: '黄金糕',
        label: '黄金糕'
      }, {
        value: '双皮奶',
        label: '双皮奶'
      }, {
        value: '蚵仔煎',
        label: '蚵仔煎'
      }],
      brandNameValue: '小米',
      // 品牌类目
      brandCategory: [{
        value: '黄金糕',
        label: '黄金糕'
      }, {
        value: '双皮奶',
        label: '双皮奶'
      }, {
        value: '蚵仔煎',
        label: '蚵仔煎'
      }],
      brandCategoryValue: '母婴',
      // 商品单位
      goodsUnit: [{
        value: '黄金糕',
        label: '黄金糕'
      }, {
        value: '双皮奶',
        label: '双皮奶'
      }, {
        value: '蚵仔煎',
        label: '蚵仔煎'
      }],
      goodsUnitValue: '个',
      //币种
      currency: [{
        value: '中国',
        label: '中国'
      }, {
        value: '日本',
        label: '日本'
      }, {
        value: '韩国',
        label: '韩国'
      }],
      currencyValue: '人民币',
      //产地
      originPlace: [{
        value: '中国',
        label: '中国'
      }, {
        value: '日本',
        label: '日本'
      }, {
        value: '韩国',
        label: '韩国'
      }],
      originPlaceValue: '中国',
      // 商品保质期限
      goodsShelfLife: '365',
      // 类目
      categoryLevel01: '1',
      categoryLevel02: '2',
      categoryLevel03: '3',
      categoryLevel04: '4',
      // 各语言标题
      chineseTitle: '',
      englishTitle: '',
      koreaTitle: '',
      japanTitle: '',

      // 各语言副标题
      chineseSubtitle: '',
      englishSubtitle: '',
      koreaSubtitle: '',
      japanSubtitle: '',
      // 语言多选框
      checkList: [],
      cnLanguage: true,
      enLanguage: true,
      krLanguage: true,
      jaLanguage: true,
      //option-name
      KoreanOptionName: '',
      ChineseOptionName: '',
      EnglishOptionName: '',
      JapaneseOptionName: '',
      //option-value
      KoreanOptionValue: '',
      ChineseOptionValue: '',
      EnglishOptionValue: '',
      JapaneseOptionValue: '',

      skuLength: '',
      skuWeight: '',
      skuHeight: '',
      skuWide: '',

      searchBarValue: '',
      optionNameValue: '',
      optionName: [{
        value: '选项1',
        label: '颜色'
      }, {
        value: '选项2',
        label: '尺寸'
      }],
      showSearchBox: false,

      showBackText: false,
      backText: ''
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (to.path.match('goodsEdit')) {
        vm.editStatus = false;
        vm.reviewStatus = false;
        vm.showStatus = true;
      } else if (to.path.match('goodsReview')) {
        vm.editStatus = false;
        vm.reviewStatus = true;
        vm.showStatus = false;
      }
    })
  },
  beforeRouteLeave(to, from, next) {
    next();
  },
  methods: {
    toggleStatus() {
      this.showStatus = !this.showStatus;
      this.editStatus = !this.editStatus;
    },
    handleClick(tab, event) {
      console.log(tab, event);
    },
    languageChange(event) {

    },
    /**
     * 语言选择，input toggle
     */
    toggleCheck(event) {
      (event.indexOf("中文") != -1) ? this.cnLanguage = false: this.cnLanguage = true;
      (event.indexOf("英文") != -1) ? this.enLanguage = false: this.enLanguage = true;
      (event.indexOf("韩文") != -1) ? this.krLanguage = false: this.krLanguage = true;
      (event.indexOf("日文") != -1) ? this.jaLanguage = false: this.jaLanguage = true;
    },

    openSearchBox() {
      this.showSearchBox = true;
    },
    closeSearchBox() {
      this.showSearchBox = false;
    },
    openBackText() {
      this.showBackText = true
    },
    cancelBackText() {
      this.showBackText = false
    },
    searchOptionName() {

    }

  }
}




